.menu-label {
    font-family: $family-sans-serif;
    font-weight: 700;
}

.menu-list {
    a {
        font-size: 1.1em;
        font-family: $family-sans-serif;
        font-weight: 400;
    }
}

.about-menu {
    // border-right: 3px solid rgba($green, 0.3);
    // text-align: right;
    height: 100%;

    > .menu-list > li {
        > a {
            background: $blue;
            color: $grey-light;
            &:before {
                font-family: FontAwesome;
                content: "\f054";
                display: inline-block;
                margin-right: 10px;
                color: transparent;
            }

            &.is-active {
                font-weight: 600;
                &:before {
                    color: $grey-light;
                }
            }

            &:hover {
                &:before {
                    color: $grey-light;
                }
            }
        } 
    }

    li ul {
        background: white;
        padding-left: 0;
        margin-top: 0;
        margin-bottom: 0;

        a { // sub menu links
            color: $grey;
            background: rgba($blue, 0.3);
            &:hover {
                background: rgba($blue, 0.3);
                &:before {
                    color: $grey;
                }
            }

            &:before {
                font-family: FontAwesome;
                content: "\f054";
                display: inline-block;
                margin-right: 10px;
                color: transparent;
            }
        }
    }
}