
.navbar-transparent {
      $bright-blue: #5bbdd4;

      @extend .is-shadowless;

      background-color: rgba($grey-light, 0);

      .navbar-end {
            > div.navbar-item {
                  & > a {
                        color: $grey-light;
                        &.is-active {
                              color: $bright-blue;
                        }
                  }
                   
                  &:hover > a, &.is-active > a {
                        color: $bright-blue;
                        background-color: transparent !important;
                  }
            }

            > a.navbar-item {
                  color: $grey-light;
                  &:hover, &.is-active {
                        color: $bright-blue;
                        background-color: transparent;
                  }
            }
      }

      .navbar-brand .logo-image {
            color: $bright-blue;
      }
}

.navbar {
      transition-property: background-color, box-shadow, transform;
      transition-duration: $navbar-transition-duration;

      font-family: $family-sans-serif;
      font-weight: 600;
      font-size: ms(0);

      a, .logo-label {
            text-shadow: 1px 1px 1px rgba($grey, 0.2) !important;
      }

      .navbar-link:not(.is-arrowless) {
            padding-right: 2em;

            &::after {
                  border-width: 2px;
                  width: 0.5em;
                  height: 0.5em; 
            }
      }

      .navbar-menu {
            text-transform: capitalize;
      }

      .navbar-end {
            > a.navbar-item,
            > .navbar-item > a.navbar-link {
                  transition-property: color;
                  transition-duration: 0.5s;
            }
      }
}

// navbar logo transparency
.logo {
      transition-property: opacity;
      transition-duration: $navbar-transition-duration;
      opacity: 1;

      // .navbar.navbar-transparent & {
      //       opacity: 0;
      //       transition-property: opacity;
      //       transition-duration: $navbar-transition-duration;
      // }
}

a.navbar-item, .navbar-item > .navbar-link {
      
      border-top: 4px solid transparent;
      border-bottom: 4px solid transparent;

      &.is-active {
            border-bottom: 4px solid $blue;
            border-top: 4px solid transparent;
      }

      .has-dropdown & {
            border: none;

            &.is-active:before {
                  font-family: "FontAwesome";
                  content: "\f054";
                  display: inline-block;
                  margin-right: .5rem;
            }
      }

}

.navbar-menu {
      max-height: 2.5rem;
}

.navbar-end > .navbar-item {
      margin-left: 1rem;
}


// dropdown animation
div.navbar-item.has-dropdown {

      &:hover > .navbar-dropdown {
            @extend .animated;
            @extend .fadeIn; 
            animation-duration: 500ms;
            visibility: visible;
      }

      & > .navbar-dropdown {
            visibility: hidden;
            display: block;
            @extend .animated;
            @extend .fadeOut;
            animation-duration: 300ms;
      }
}